








import { Component, Mixins, Watch, Prop } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import debounce from "lodash.debounce";

@Component({
    name: "MyInputInt"
})
export default class MyInputInt extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop({})
    placeholder!: string;

    /** 内部绑定数据 */
    inputValue = "";

    reg = /[^(\d|\-)]/g;

    /** 转化后的最小值 */
    get minNumber() {
        const { min } = this.$attrs;
        let result = NaN;
        if (typeof min !== "undefined") {
            result = parseInt(min.toString());
        }

        return result;
    }

    /** 转化后的最大值 */
    get maxNumber() {
        const { max } = this.$attrs;
        let result = NaN;
        if (typeof max !== "undefined") {
            result = parseInt(max.toString());
        }

        return result;
    }

    get finalPlaceholder() {
        const { placeholder, minNumber, maxNumber } = this;

        let result = "";
        if (placeholder) {
            result = placeholder;
        } else if (!isNaN(minNumber) && !isNaN(maxNumber)) {
            result = `请输入${minNumber}至${maxNumber}之间的整数`;
        } else if (!isNaN(minNumber) && isNaN(maxNumber)) {
            result = `请输入大于${minNumber}的整数`;
        } else if (isNaN(minNumber) && !isNaN(maxNumber)) {
            result = `请输入小于${maxNumber}的整数`;
        } else if (isNaN(minNumber) && isNaN(maxNumber)) {
            result = `请输入整数`;
        }

        return result;
    }

    /** 输入数值校正 */
    numericalCorrection(val: string | number) {
        let temp = val.toString();

        const regs = [
            { reg: /[^(\d|\-)]/g, replace: "" }, // 去非数字
            { reg: /(?<=(^(\d|\-).*))-/g, replace: "" }, // 去除第二个负号
            { reg: /^0(\d+)$/, replace: "$1" } // 后面有数字时去除首位0
        ];

        for (let i = 0; i < regs.length; i++) {
            temp.replace && (temp = temp.replace(regs[i]["reg"], regs[i]["replace"]));
        }

        return temp;
    }

    /** 计算最终值 */
    calcFinalValue(val: string) {
        let result = val;

        if (!isNaN(parseInt(result))) {
            const valNumber = parseInt(val);
            const { minNumber, maxNumber } = this;

            if (!isNaN(valNumber)) {
                !isNaN(minNumber) && valNumber < minNumber && (result = minNumber.toString());

                !isNaN(maxNumber) && valNumber > maxNumber && (result = maxNumber.toString());
            }
        }

        return result;
    }

    /** 改变绑定的数值：去弹跳 */
    debounceEmitValue = debounce(function(this: MyInputInt) {
        this.$nextTick(() => {
            const finalValue = this.calcFinalValue(this.inputValue);

            if (finalValue !== "-") {
                // 只输了一个负号不发送值
                this.inputValue = finalValue;
                this.currentValue = finalValue;
            }
        });
    }, 350);

    @Watch("currentValue", {
        immediate: true
    })
    watchCurrentValue(newValue: any, oldValue: any) {
        if (this.currentValue || this.currentValue == "0" || this.currentValue == "") {
            this.inputValue = this.currentValue = this.calcFinalValue(
                this.numericalCorrection(this.currentValue)
            );
        }
    }

    @Watch("inputValue")
    watchInputValue(newValue: any, oldValue: any) {
        this.$nextTick(() => {
            const result = newValue ? this.numericalCorrection(newValue) : newValue;
            this.inputValue = result;

            this.debounceEmitValue();
        });
    }
}
